@import "mixin/baseImport";
@import "_sprite/flashcard_sprite";

%card-select-icon{
	&.active{
		&:before{content:"";position: absolute;top: 50%;left: 50%;width: 569px;height: 570px;margin: -285px 0 0 -285px;animation: card-select-icon 0.7s steps(8) 1 forwards;background: url("/assets/images/sprite/flashcard_select1.png") no-repeat 0 0;}
	}
}
%puzzle-complete-motion1-icon{
	&:before{content:"";position: absolute;top: 50%;left: 50%;width: 878px;height: 768px;margin: -384px 0 0 -439px;animation: puzzle-complete-motion1-icon 1.1s steps(11) 1 forwards;background: url("/assets/images/sprite/flashcard_puzzle_complete1.png") no-repeat 0 0;}
}
%puzzle-complete-motion2-icon{
	&:after{content:"";position: absolute;top: 50%;left: 50%;width: 1021px;height: 765px;margin: -384px 0 0 -510px;animation: puzzle-complete-motion2-icon 1.1s steps(4) 1 forwards;background: url("/assets/images/sprite/flashcard_puzzle_complete2.png") no-repeat 0 0;}
}

.main-flashcard01{position: relative;width: 952px;height: 675px;margin: 0 auto;z-index: $z-layer1;overflow: hidden;background: url("/assets/images/flashcard/bg_main01.png") no-repeat 0 0;
	&__header{
		.btn{transition: all 0.3s;border-radius: 5px;
			&-box{position: absolute;top: 22px;right: 37px;}
			&{float: left;margin-left: 7px;
				&:nth-of-type(1){margin-left: 0;}
				&-help{width: 50px;height: 50px;}
				&-close{width: 50px;height: 50px;}
			}
		}

		.icon{display: block;width: 100%;height: 100%;border-radius: 5px;background-color: transparent;
			&-help{background: url("/assets/images/flashcard/icon_help01.png") no-repeat 0 0;}
			&-close{background: url("/assets/images/flashcard/icon_close01.png") no-repeat 0 0;}
		}
	}
	&__content{
		.flashcard{margin-top: 160px;
			&__items{width: 950px;height: 451px;margin: 0 auto;display: flex;align-items: center;justify-content: center;flex-wrap: wrap;
				&.length{
					&1{.flashcard__item{margin: 10px 22px;}}
					&2{.flashcard__item{margin: 10px 22px;}}
					&3{.flashcard__item{margin: 10px 22px;}}
					&4{.flashcard__item{margin: 10px 22px;}}
					&5{.flashcard__item{margin: 10px 9px;}}
					&6{width: 586px;
						.flashcard__item{margin: 10px 9px;}
					}
					&7{width: 750px;
						.flashcard__item{margin: 10px 9px;
							&:nth-of-type(1){margin-left: 70px;}
							&:nth-of-type(3){margin-right: 70px;}
						}
					}
					&8{width: 750px;
						.flashcard__item{margin: 10px 9px;}
					}
					&9{
						.flashcard__item{margin: 10px 9px;
							&:nth-of-type(1){margin-left: 70px;}
							&:nth-of-type(4){margin-right: 70px;}
						}
					}
					&10{.flashcard__item{margin: 10px 9px;}}
				}
			}
			&__item{position: relative;width: 156px;height: 216px;transition: all 0.3s;
				&.default{overflow: hidden;background: url("/assets/images/flashcard/bg_card_default.png") no-repeat 0 0;
					.img{opacity: 0;}
				}
				&.complete{overflow: hidden;background: url("/assets/images/flashcard/bg_card_complete.png") no-repeat 0 0;
					.img{opacity: 1;}
				}
				&.active{overflow: hidden;background: url("/assets/images/flashcard/bg_card_default.png") no-repeat 0 0;
					img{opacity: 0;}
					&.motion{overflow: visible;
						.icon{
							&-oscar{top: -90px;left: -106px;animation: oscar-icon 0.583s steps(6) 1 forwards;background: url("/assets/images/sprite/flashcard_oscar1.png") no-repeat 0 0;}
						}
					}
					.icon{
						&-oscar{position: absolute;top: -128px;left: -157px;width: 417px;height: 343px;background: url("/assets/images/flashcard/icon_card_active.png") no-repeat 0 0;}
					}
				}
			}
			.btn{
				&-card{width: 100%;height: 100%;
					img{width: 150px;height: auto;}
				}
			}
		}
	}
}

.main-flashcard-detail01{position: relative;width: 1024px;height: 653px;margin: 0 auto;
	&__content{
		.swiper{
			&-container{width: 1024px;height: 653px;border-radius: 10px;overflow: hidden;
				&.active{overflow: visible;}
			}
			&-wrapper{position: relative;height: 100%;}
			&-slide{position: relative;
			}
			&-slide-active{&.motion{transform: scale(1);transition: all 0.3s}}
			&-slide-prev{transform: scale(0.4);
				&.motion{transition: all 0.3s}
			}
			&-slide-next{transform: scale(0.4);
				&.motion{transition: all 0.3s}
			}
			//&-slide-active{transform: scale(1);}
			&-controller{
				.btn-swiper-prev{position: absolute;top: 50%;left: 86px;width: 63px;height: 115px;margin-top: -67px;background: url("/assets/images/flashcard/popup/icon_prev01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;z-index: 1;
					&.swiper-button-disabled{opacity: 0;}
				}
				.btn-swiper-next{position: absolute;top: 50%;right: 86px;width: 63px;height: 115px;margin-top: -67px;background: url("/assets/images/flashcard/popup/icon_next01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;z-index: 1;
					&.swiper-button-disabled{opacity: 0;}
				}
			}
		}
		.flashcard{margin-top: -10px;
			&__items{position: relative;height: 653px;width: 708px;padding: 44px 70px 44px 67px;margin: 0 auto;box-sizing:border-box;background: url("/assets/images/flashcard/popup/bg_flashcard.png") no-repeat 0 0;}
			&__item{position: relative;width: 290px;height: 285px;
				.puzzle{
					&-img{overflow: hidden;width: 100%;height: 100%;display: flex;align-items: center;justify-content: center;flex-wrap: wrap;
						img{max-width: 574px;height: auto;}
					}
					&-txt{position: absolute;bottom: 40px;left: 0;width: 100%;text-align: center;
						&.txt{
							&-high{@include font($noto, $font-b, 54px, 54px, #3b1e12);}
							&-low{@include font($noto, $font-b, 70px, 60px, #3b1e12);}
						}
					}
				}
				&.puzzle{
					&-steadfast{position: relative;width: 100%;height: 566px;
						&.complete{
							@extend %puzzle-complete-motion1-icon;
							@extend %puzzle-complete-motion2-icon;
						}
					}
					&-top-left{position: absolute;top: 44px;left: 67px;@extend %card-select-icon;
						&.complete{display: none;}
					}
					&-top-right{position: absolute;top: 44px;right: 70px;@extend %card-select-icon;
						&.complete{display: none;}
					}
					&-btm-left{position: absolute;bottom: 44px;left: 67px;@extend %card-select-icon;
						&.complete{display: none;}
					}
					&-btm-right{position: absolute;bottom: 44px;right: 70px;@extend %card-select-icon;
						&.complete{display: none;}
					}
				}
			}
			.icon{display: block;
				&-oscar{position: absolute;top: -128px;left: -157px;width: 474px;height: 475px;
				}
			}
			.btn{
				&-card{width: 100%;height: 100%;}
			}
		}
	}
	.btn{overflow: visible;
		&-close{position: absolute;top: 20px;right: 16px;width: 44px;height: 44px;}
		&-puzzle-top-left{width: 290px;height: 285px;}
		&-puzzle-top-right{width: 290px;height: 285px;}
		&-puzzle-btm-left{width: 290px;height: 285px;}
		&-puzzle-btm-right{width: 290px;height: 285px;}
	}

	.icon{display: block;background-color: transparent;
		&-oscar{position: absolute;top: -128px;left: -157px;width: 474px;height: 475px;animation: oscar-icon 1s steps(9) 1 forwards;background: url("/assets/images/sprite/flashcard_oscar1.png") no-repeat 0 0;}
		&-close{width: 100%;height: 100%;transition: all 0.3s;border-radius: 5px;
			background: url("/assets/images/flashcard/popup/icon_close01.png") no-repeat 0 0;
		}
		&-puzzle-top-left{position: absolute;top: 0;left: 0;
			&.type1{width: 368px;height: 285px;background: url("/assets/images/flashcard/popup/type1/img_card1.png") no-repeat 0 0;}
			&.type2{width: 295px;height: 379px;background: url("/assets/images/flashcard/popup/type2/img_card1.png") no-repeat 0 0;}
		}
		&-puzzle-top-right{position: absolute;top: 0;right: 0;
			&.type1{width: 289px;height: 364px;background: url("/assets/images/flashcard/popup/type1/img_card2.png") no-repeat 0 0;}
			&.type2{width: 373px;height: 291px;background: url("/assets/images/flashcard/popup/type2/img_card2.png") no-repeat 0 0;}
		}
		&-puzzle-btm-left{position: absolute;bottom: 0;left: 0;
			&.type1{width: 289px;height: 364px;background: url("/assets/images/flashcard/popup/type1/img_card3.png") no-repeat 0 0;}
			&.type2{width: 384px;height: 287px;background: url("/assets/images/flashcard/popup/type2/img_card3.png") no-repeat 0 0;}
		}
		&-puzzle-btm-right{position: absolute;bottom: 0;right: 0;
			&.type1{width: 368px;height: 285px;background: url("/assets/images/flashcard/popup/type1/img_card4.png") no-repeat 0 0;}
			&.type2{width: 288px;height: 365px;background: url("/assets/images/flashcard/popup/type2/img_card4.png") no-repeat 0 0;}
		}
	}
}

